.courseDetail {
  padding: 10px 20px;
  margin: 0 auto;
  align-items: center;
}
.button {
  padding: 10px 50px;
  border-radius: 20px;
  border: none;
  background: linear-gradient(45deg, #7f6ccc, #4b2bca, #110446);
  font-weight: 600;
  color: white;
}
.courseWrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  gap: 50px;
  padding: 10px;
}

.courseImg {
  min-width: 15%;
  max-width: 85vw;
  object-fit: contain;
  align-self: flex-start;
}
.courseImg:focus {
  transform: scale(1.2, 1.2) rotate(5deg);
}
.courseInfo {
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0;
}
.title {
  font-size: 45px;
}
.text {
  font-size: 20px;
  margin: 0;
}
@media (min-width: 780px) {
  .courseImg {
    min-width: 15%;
    object-fit: contain;
    align-self: flex-start;
  }
  .courseWrap {
    display: flex;
    flex-direction: row;
    padding: 10px 35px;
  }
  .courseDetail {
    padding: 30px 50px;
  }
}
