.box {
  display: inline-block;
  height: 200px;
  width: 33.3%;
  float: left;
  position: relative;
  /*margin:0 -4px -5px -2px;*/
  transition: all 0.2s ease;
}
.loader3 {
  position: relative;
  width: 150px;
  height: 20px;

  top: 45%;
  top: -webkit-calc(50% - 10px);
  top: calc(50% - 10px);
  left: 25%;
  left: -webkit-calc(50% - 75px);
  left: calc(50% - 75px);
}

.loader3:after {
  content: 'LOADING ...';

  color: #000000;
  font-weight: 200;
  font-size: 20px;
  position: absolute;
  width: 100%;
  height: 20px;
  line-height: 20px;
  left: 0;
  top: -10px;
  background-color: #ffffff;
  z-index: 1;
}

.loader3:before {
  content: '';
  position: absolute;
  background-color: #1900fa;
  top: -15px;
  left: 0px;
  height: 30px;
  width: 0px;
  z-index: 0;
  opacity: 1;
  -webkit-transform-origin: 100% 0%;
  transform-origin: 100% 0%;
  -webkit-animation: loader3 10s ease-in-out infinite;
  animation: loader3 10s ease-in-out infinite;
}

@-webkit-keyframes loader3 {
  0% {
    width: 0px;
  }
  70% {
    width: 100%;
    opacity: 1;
  }
  90% {
    opacity: 0;
    width: 100%;
  }
  100% {
    opacity: 0;
    width: 0px;
  }
}

@keyframes loader3 {
  0% {
    width: 0px;
  }
  70% {
    width: 100%;
    opacity: 1;
  }
  90% {
    opacity: 0;
    width: 100%;
  }
  100% {
    opacity: 0;
    width: 0px;
  }
}
